.row {
  position: relative;
  margin: 5px;
  max-width: 1080px;
  display: block;
  align-self: center;
  display: flex;
}

.Experiences {
  float: left;
  width: 800px;
  margin: 15px;
  padding: 5;
  text-align: left;
}

.Projects {
  text-align: left;
  margin-right: 0px;
  margin-top: 15px;
  float: right;
  width: 30%;
  padding: 5;
  overflow: hidden;
  margin-bottom: 100px;
}

@media screen and (max-width: 1000px) {
  .Experiences {
    position:relative;
    float: none;
    width: 100%;
    margin: 15px;
    padding: 5;
    text-align: left;
  }
  
  .Projects {
    position:relative;
    text-align: left;
    margin-right: 0px;
    margin-top: 15px;
    float: none;
    width: 100%;
    padding: 5;
  }
}

.path1, .path2 {
  position: fixed;
  width: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  z-index: 2;
}

.path1 {
  right: 0%;
  width: 96%;
  top: -90%;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  -webkit-animation: dash1 5s linear forwards;
          animation: dash1 5s linear forwards;
}

.path2 {
  stroke-dashoffset: 1000;
  stroke-dasharray: 1000;
  -webkit-animation: dash2 10s linear forwards;
          animation: dash2 10s linear forwards;
  left: -35%;
  bottom: -50%;
}

@-webkit-keyframes dash1 {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes dash1 {
  to {
    stroke-dashoffset: 0;
  }
}

@-webkit-keyframes dash2 {
  
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes dash2 {
  
  to {
    stroke-dashoffset: 0;
  }
}

.bg-shape {
  position: absolute;
  top: 0;
  overflow: hidden;
  display: flex;
}


.App {
-webkit-animation: fadein 2s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
            animation: fadein 2s;
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Firefox < 16 */

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Internet Explorer */

/* Opera < 12.1 */

  .Projects, .Experiences {
  -webkit-animation: fadein 4s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
            animation: fadein 4s;
  z-index: 1;
}
.image {
    animation: fadeIn ease 20s;
    -webkit-animation: fadeIn ease 20s;
    -moz-animation: fadeIn ease 20s;
    -o-animation: fadeIn ease 20s;
    -ms-animation: fadeIn ease 20s;
  
    border-radius: 10px;
    width: 400px;
    height: 400px;
    transition:         transform .8s ease-in-out;
  } 
.image:hover {
    transform: rotate(20deg);
  }


